.header {
    border-bottom: 1px solid rgba(#FFFFFF, 0.1);
    padding: 36px 72px;
    font-size: 16px;
    line-height: 114%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: all .3s;
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1920px;
        margin: 0 auto;
    }
    &__logo {
        width: 126px;
        display: block;
        margin-right: 65px;
        flex-shrink: 0;
        svg {
            width: 100%;
        }
    }
    &__mail {
        display: block;
        margin: 0 60px;
        &:hover {
            border-color: #2D9586;
            color: #2D9586;
        }
    }
    &__phone {
        display: flex;
        align-items: center;
        svg {
            margin-right: 11px;
        }
        &:hover {
            border-color: #2D9586;
            color: #2D9586;
            path {
                fill: #2D9586;
            }
        }
    }
    &__lang {
        width: 110px;
        flex-shrink: 0;
        margin-left: 50px;
    }
    &__contacts {
        display: flex;
        align-items: center;
    }
    &--active {
        background: #FBFBFB;
        color: #23272B;
        position: fixed;
        .logo-white {
            fill: #23272B;
        }
        .header__phone {
            path {
                stroke: #2D9586;
            }
        }
        .lang {
            &-show {
                border-color: #CCD0D3;
                path {
                    fill: #CCD0D3;
                }
            }
        }
        .header__burger {
            span {
                background: #23272B;
            }
        }
    }
}

.lang {
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-sizing: border-box;
    border-radius: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px;
    &__item {
        opacity: 0.2;
        cursor: pointer;
        transition: all .3s;
        &:hover {
            opacity: 1;
        }
        &--active {
            opacity: 1;
            pointer-events: none;
        }
    }
    @media screen and (max-width: 992px) {
        &-show {
            height: 45px;
            padding: 0 20px;
        }
    }
}

.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    &__item {
        border: 1px solid transparent;
        padding: 0 15px;
        border-radius: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        &--active,
        &:hover {
            border-color: #2D9586;
            color: #2D9586;
        }
    }
}

@media screen and (max-width: 1600px) {
    .header {
        padding-left: 0;
        padding-right: 0;
        &__inner {
            max-width: 1440px;
            padding: 0 15px;
        }
        &__logo {
            margin-right: 10px;
        }
        &__mail {
            margin: 0 20px;
        }
        &__lang {
            margin-left: 20px;
        }
    }
    .nav {
        &__item {
            padding: 0 8px;
            font-size: 15px;
        }
    }
}

@media screen and (max-width: 1200px) {
    .header {
        padding: 25px 0;
        &__inner {
            max-width: 970px;
            justify-content: flex-end;
        }
        &__logo {
            margin-right: auto;
        }
        &__burger {
            z-index: 11;
            display: flex;
            align-items: center;
            position: relative;
            width: 30px;
            height: 21px;
            margin-left: 30px;
            span {
                width: 100%;
                height: 2px;
                border-radius: 2px;
                background: #fff;
                display: block;
                transition: all .3s;
                &:nth-child(1) {
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                &:nth-child(3) {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }
            &--active {
                span {
                    &:nth-child(1) {
                        transform: rotate(-45deg);
                        top: 10px;
                    }
                    &:nth-child(2) {
                        transform: scale(0);
                    }
                    &:nth-child(3) {
                        transform: rotate(45deg);
                        bottom: 9px;
                    }
                }
            }
        }
        &--active {
            .nav {
                color: #fff;
            }
        }
    }
    .nav {
        position: fixed;
        left: -100%;
        top: 0;
        background: #3C444D;
        height: 100vh;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        padding: 50px;
        transition: all .3s;
        z-index: 10;
        overflow-y: auto;
        &--active {
            left: 0;
            &~.header__burger {
                span {
                    background: #fff;
                }
            }
        }
        &__item {
            font-size: 22px;
            display: block;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .header {
        &__lang {
            width: 95px;
            margin-left: 15px;
        }
        &__burger {
            margin-left: 15px;
        }
        &__mail {
            margin-right: 15px;
        }
    }
}

@media screen and (max-width: 768px) {
    .header {
        &__contacts {
            flex-wrap: wrap;
            max-width: 150px;
            margin-top: 50px;
        }
        &__mail {
            margin: 10px 0 0;
            width: 100%;
            order: 2;
        }
        &__phone {
            // width: 100%;
        }
    }
}