body {
    font-family: 'Gilroy', sans-serif;
    font-weight: 400;
    height: 100%;
    overflow-x: hidden;
    color: #23272B;
    letter-spacing: 0.02em;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 15px;
    width: 100%;
    @media screen and (max-width: 1200px) {
        max-width: 970px;
    }
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-green-outline {
    border: 1px solid #2D9586;
    color: #2D9586;
    &:hover {
        background: #2D9586;
        color: #FFFFFF;
        span {
            text-decoration: none;
        }
        path {
            fill: #fff;
        }
    }
    &:active {
        background: #1B655C;
        border-color: #1B655C;
        color: #FFFFFF;
        path {
            fill: #fff;
        }
    }
}

.btn-green {
    background: #2D9586;
    color: #fff;
    &:hover {
        background: #157C6D;
        color: #fff;
    }
    &:active {
        background: #1B655C;
        color: #fff;
    }
}

.display1 {
    font-size: 45px;
    line-height: 120%;
    @media screen and (max-width: 1200px) {
        font-size: 38px;
    }
    @media screen and (max-width: 992px) {
        font-size: 32px;
    }
    @media screen and (max-width: 768px) {
        font-size: 25px;
    }
}

.tab-slider {
    display: flex;
    align-items: center;
    &__item {
        width: unset;
        display: flex;
        align-items: center;
        height: 44px;
        background: #F9F9F9;
        border-radius: 60px;
        padding: 0 25px;
        font-size: 14px;
        flex-shrink: 0;
        white-space: nowrap;
        transition: all .3s;
        svg {
            margin-right: 14px;
        }
        &:hover {
            background: #EDEDED;
            cursor: pointer;
        }
        &:active {
            color: #fff;
            background: #2D9586;
            path {
                fill: #fff;
            }
        }
        &--active {
            background: #2D9586;
            pointer-events: none;
            color: #fff;
            path {
                fill: #fff;
            }
        }
    }
    &__prev,
    &__next {
        position: static;
        margin: 0;
    }
    .swiper-container {
        overflow: hidden;
        margin: 0 20px;
    }
    &__content {
        display: none;
        &--active {
            display: block;
        }
    }
    @media screen and (max-width: 768px) {
        width: calc(100% + 30px);
        margin-left: -15px;
        &__item {
            padding: 0 12px;
            font-size: 12px;
            svg {
                margin-right: 5px;
            }
        }
        .swiper-container {
            margin: 0 5px;
        }
    }
}