.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #000;
    opacity: 0.7;
    z-index: 20;
}

.modal {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    width: 1410px;
    max-width: calc(100% - 20px);
    background-color: #fff;
    z-index: 21;
    overflow-y: auto;
    max-height: calc(100vh - 25px);
    opacity: 0;
    pointer-events: none;
    &--active {
        opacity: 1;
        pointer-events: unset;
    }
    &__wrap {
        display: flex;
    }
    &__close {
        position: absolute;
        top: 20px;
        right: 20px;
        &:hover {
            path {
                fill-opacity: 1;
            }
        }
    }
    &__img {
        width: 525px;
        flex-shrink: 0;
        max-width: 100%;
        img {
            width: 100%;
            display: block;
        }
    }
    &__right {
        padding: 55px 70px;
    }
    &__title {
        font-size: 30px;
        line-height: 120%;
    }
    &__co {
        margin-top: 30px;
        display: flex;
        align-items: center;
        &-icon {
            background-image: url(../img/circle1.svg), url(../img/circle2.svg);
            background-size: 100% 100%, 40px auto;
            background-position: 0 0, right 1px center;
            background-repeat: no-repeat;
            margin-right: 16px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 45px;
            height: 45px;
            flex-shrink: 0;
            svg {
                max-width: 25px;
                max-height: 22px;
            }
        }
        &-title {
            color: #2D9586;
            font-size: 13px;
            line-height: 138%;
            max-width: 230px;
        }
    }
    &__date {
        color: #2D9586;
        margin-top: 30px;
    }
    &__desc {
        margin-top: 40px;
        p {
            font-size: 15px;
            line-height: 187%;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .modal {
        padding: 0 20px 20px;
        &__wrap {
            display: block;
        }
        &__top {
            position: sticky;
            top: 0;
            width: 100%;
            background-color: #fff;
            padding: 15px 0;
            display: flex;
            justify-content: flex-end;
        }
        &__right {
            width: 100%;
            margin-top: 20px;
            padding: 0;
        }
        &__close {
            position: static;
            top: 0;
            right: 0;
        }
        &__title {
            max-width: 100%;
            font-size: 24px;
        }
        &__co {
            margin-top: 10px;
        }
        &__desc {
            margin-top: 15px;
        }
    }
}