.footer {
    background: #3C444D;
    color: #FFFFFF;
    padding: 80px 0 30px;
    &-top,
    &-bottom {
        &__inner {
            display: flex;
            justify-content: space-between;
        }
    }
    &-bottom {
        margin-top: 70px;
    }
    &__col {
        width: 240px;
        &:last-child {
            width: 330px;
            display: flex;
            justify-content: space-between;
        }
    }
    &__logo {
        width: 210px;
        display: block;
        img {
            width: 100%;
        }
    }
    &__link {
        font-size: 14px;
        line-height: 229%;
        display: block;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
        &:hover {
            color: #2D9586;
        }
    }
    &__contacts {
        width: 240px;
        &-title {
            font-size: 14px;
            line-height: 229%;
        }
        &-items {
            margin-top: 17px;
        }
        &-item {
            display: flex;
            align-items: center;
            font-size: 15px;
            line-height: 176%;
            &:not(:last-child) {
                margin-bottom: 13px;
            }
            svg {
                margin-right: 19px;
            }
        }
    }
    &__lang {
        &.lang {
            padding: 0;
            height: unset;
            border: none;
            align-items: flex-start;
            justify-content: flex-start;
            padding-top: 7px;
        }
        .lang {
            &__item {
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }
    }
    &__logo2 {
        width: 117px;
        display: block;
        svg {
            width: 100%;
        }
        &:hover {
            .logo-red {
                fill: #F04D44;
            }
        }
    }
    &__link2 {
        color: #848A92;
        font-size: 14px;
        line-height: 229%;
    }
    a.footer__link2 {
        &:hover {
            color: #2D9586;
        }
    }
    a.footer__contacts-item {
        &:hover {
            color: #2D9586;
            path {
                fill: #2D9586;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .footer {
        &__logo {
            width: 160px;
        }
        &__col {
            width: 200px;
            &:last-child {
                width: 290px;
            }
        }
        &__link {
            line-height: 130%;
            &:not(:last-child) {
                margin-bottom: 25px;
            }
        }
        &__link2 {
            line-height: 125%;
        }
        &__contacts {
            &-title {
                line-height: 130%;
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .footer {
        padding: 35px 0;
        &-top,
        &-bottom {
            &__inner {
                display: block;
            }
        }
        &-bottom {
            margin-top: 25px;
        }
        &__col {
            width: 100% !important;
            flex-direction: column;
            &:not(:last-child) {
                margin-bottom: 25px;
            }
        }
        &__contacts {
            width: 100%;
            margin-bottom: 25px;
        }
        &__lang {
            display: flex;
        }
    }
}