.first-screen {
    color: #FFFFFF;
    position: relative;
}

.main {
    position: relative;
    &__next-screen {
        position: absolute;
        transform: rotate(-90deg) translateY(-50%);
        left: 20px;
        top: 42%;
        z-index: 4;
        display: flex;
        align-items: center;
        font-size: 15px;
        svg {
            transform: rotate(90deg);
            margin-right: 35px;
        }
        span {
            padding-top: 2px;
        }
        &:hover {
            color: #2D9586;
            path {
                fill: #2D9586;
            }
        }
    }
    &__slide {
        height: 100vh;
        background-size: cover;
        padding: 140px 0;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: rgba(17, 23, 29, 0.5);
            pointer-events: none;
        }
    }
    .container {
        display: flex;
        align-items: center;
        height: 100%;
    }
    &__inner {
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: relative;
        z-index: 2;
    }
    &__title {
        font-weight: 400;
        font-size: 50px;
        line-height: 132%;
        max-width: 795px;
        margin-right: 20px;
    }
    &__items {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 25px 20px;
        width: 444px;
        flex-shrink: 0;
    }
    &__item {
        background: rgba(255, 255, 255, 0.1);
        border: 1px solid rgba(255, 255, 255, 0.1);
        backdrop-filter: blur(40px);
        border-radius: 4px;
        padding: 43px 23px 23px;
        text-align: center;
        &-num {
            font-size: 48px;
            line-height: 22px;
        }
        &-title {
            font-size: 17px;
            line-height: 22px;
            display: block;
            margin-top: 19px;
        }
    }
    &__pag {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        position: static;
        max-width: 1440px;
        padding: 0 15px;
        margin: 70px auto 0;
        &-wrap {
            z-index: 3;
            width: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%);
            margin-top: 50px;
        }
    }
    .swiper-pagination-bullet {
        width: 6px;
        height: 6px;
        background-color: #fff;
        border-radius: 50%;
        margin-top: 10px;
        opacity: 1;
        &:not(:last-child) {
            margin-right: 40px;
        }
    }
    .swiper-pagination-bullet-active {
        background: #30C3AD;
    }
}

.sponsor {
