.geography {
    padding: 50px 0 0;
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__buttons {
        display: flex;
        align-items: center;
    }
    &__btn {
        padding: 0 23px;
        height: 65px;
        border-radius: 60px;
        font-size: 16px;
        svg {
            margin-right: 15px;
        }
        &:not(:last-child) {
            margin-right: 16px;
        }
    }
    &__tabs {
        margin-top: 52px;
    }
    &__inner {}
    &__map {
        width: 1410px;
        position: relative;
        &-block {
            overflow-x: auto;
            padding-bottom: 90px;
        }
        img {
            width: 100%;
        }
    }
    &__marker {
        position: absolute;
        width: 180px;
        &--active {
            .geography__marker {
                &-show {
                    z-index: 3;
                    background: unset;
                    backdrop-filter: unset;
                    &:before {
                        opacity: 0;
                    }
                }
                &-hidden {
                    opacity: 1;
                    pointer-events: unset;
                }
                &-arrow {
                    transform: rotate(180deg);
                }
            }
        }
        &-show {
            height: 45px;
            display: flex;
            align-items: center;
            position: relative;
            padding-right: 14px;
            cursor: pointer;
            background: rgba(255, 255, 255, 0.8);
            backdrop-filter: blur(10px);
            border-radius: 60px;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 100%;
                height: 100%;
            }
        }
        &-icon {
            width: 45px;
            height: 100%;
            border-radius: 50%;
            position: relative;
            z-index: 2;
            img {
                width: 12px;
            }
            &:after {
                content: '';
                position: absolute;
                z-index: 1;
                background: inherit;
                width: 5px;
                height: 5px;
                bottom: 0;
                left: 17px;
                transform: rotate(-45deg) translateY(3px);
            }
        }
        &-title {
            position: relative;
            z-index: 2;
            font-size: 13px;
            margin-left: 14px;
        }
        &-arrow {
            margin-left: auto;
            display: block;
            position: relative;
            z-index: 2;
            background-color: transparent;
        }
        &-hidden {
            position: relative;
            z-index: 2;
            padding: 56px 12px 20px;
            opacity: 0;
            pointer-events: none;
            position: absolute;
            top: 0;
            left: 0;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                background: rgba(255, 255, 255, 0.8);
                backdrop-filter: blur(10px);
                border-radius: 20px;
                // opacity: 0;
                pointer-events: none;
            }
        }
        &-desc {
            font-size: 10px;
            line-height: 170%;
            position: relative;
            z-index: 2;
        }
        &-btn {
            height: 38px;
            width: 100%;
            margin-top: 16px;
            border-radius: 60px;
            font-size: 13px;
            position: relative;
            z-index: 2;
        }
    }
}

