                    text-decoration: underline;
                }
                path {
                    fill: #157C6D;
                }
            }
            &:active {
                color: #1B655C;
                span {
                    text-decoration: none;
                }
                path {
                    fill: #1B655C;
                }
            }
        }
        &-date {
            font-size: 13px;
            color: #2D9586;
        }
    }
}

.map {
    position: relative;
    height: 790px;
    margin-top: 80px;
    .container {
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        left: 50%;
        z-index: 2;
        width: 1410px;
        @media screen and (max-width: 1440px) {
            width: calc(100% - 30px);
        }
    }
}

.contacts {
    padding: 50px 0 0;
    #map {
        width: 100%;
        height: 100%;
    }
    &>img {
        width: 100%;
        display: block;
        height: 100%;
    }
    &-info {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 3;
        background: #2D9586;
        backdrop-filter: blur(15px);
        color: #fff;
        padding: 43px 36px 32px;
        width: 330px;
        &__item {
            display: none;
            &--active {
                display: block;
            }
        }
    }
    &-cities {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 3;
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(15px);
        width: 330px;
        &__item {
            padding: 19px 40px;
            font-size: 18px;
            line-height: 178%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            transition: all .3s;
            &:hover {
                background: #2D9586;
                color: #fff;
                cursor: pointer;
            }
            svg {
                display: none;
            }
            &--active {
                background: #2D9586;
                width: calc(100% + 15px);
                color: #fff;
                pointer-events: none;
                svg {
                    display: unset;
                }
            }
        }
    }
    &__title {
        font-size: 26px;
        line-height: 123%;
        margin-bottom: 36px;
    }
    &__item {
        display: flex;
        font-size: 15px;
        line-height: 167%;
        &:not(:last-child) {
            margin-bottom: 30px;
        }
        svg {
            margin-top: 6px;
            margin-right: 17px;
        }
    }
}

